<script>
    export default {
        name: 'HeaderComp',
        data(){
            return {
                cartAmount : -1
            }
        },
        mounted() {
            let ref = this
            this.$CoreAPI.on("cart-updated",()=>{
                ref.cartAmount = ref.$CoreAPI.client_getTotalAmounts();
            })
            ref.cartAmount = ref.$CoreAPI.client_getTotalAmounts()
        },
    }
    
</script>

<template>
    <header class="header">
        <div class="header__icon">
            <img @click="$router.push('/')" class="header__icon-item" src="../assets/icons/titl.svg" alt="titl.vue">
        </div>

        <nav class="navigation">
            <ul class="navigation__ul">
                <li class="navigation__ul-item"><a href="/" class="navigation__ul-item-href">Главная</a></li>
                <li class="navigation__ul-item"><a href="#catalog" class="navigation__ul-item-href">Каталог <img class="icon" src="../assets/icons/arrow.svg" alt="list"></a></li>
                <li class="navigation__ul-item" v-if="!$store.state.user"><a @click="$CoreAPI.devAuth()" href="#" class="navigation__ul-item-href">Dev Auth</a></li>
            </ul>
        </nav>
        
        <div class="header__container">
            <form class="header__container-form">
                <button class="header__container-form-btn" type="submit">
                    <!-- <img class="icon" src="../assets/icons/find.svg"> -->
                    ф
                </button>
                <input class="header__container-form-input" name="find" type="text" placeholder="Поиск">
            </form>
    
            <div class="header__cart">
                <RouterLink to="/cart"><img class="icon" src="../assets/icons/cart.svg"> <div class="point" v-if="cartAmount > 0">{{cartAmount}}</div></RouterLink>
               
            </div>
        </div>
    </header>
</template>
<style scoped>
    .header__cart{
        position: relative;
    }
    .point{
        height: 25px;
        font-size: 14px;
        width: 25px;
        color: #fff;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: red;
        position: absolute;
        right: 0;
        border-radius: 50%;
        bottom: -5px;
    }
</style>
