<template>
    <div class="order">
        <HeaderComp></HeaderComp>
        <form class="form" ref="form" @submit.prevent>
            <h1 style="font-weight: 100; font-size: 1.4em; text-transform: uppercase;">Оформление заказа</h1>
            <h2>Контактные данные</h2>
            <input type="text" placeholder="ФИО" name="fio">
            <input type="text" placeholder="EMAIL" name="email">
            <input type="text" placeholder="НОМЕР ТЕЛЕФОНА" name="phone">
            <h2>Товары в корзине</h2>
            <div class="cart_table">
                <div class="cart_elem" v-for="prod of cart" :key="prod.id">
                    <div class="cart_elem_image">
                        <img :src="prod.images[0].path" height="100" v-if="prod.images.length > 0">
                        <img src="@/assets/icons/product_none.png" height="100" v-else>
                    </div>
                    <div class="cart_elem_meta_group">
                        <span class="cart_elem_name">{{ prod.name }}</span>
                        <span class="cart_elem_prop">Размер: {{ $Helper.findSizeNameByID(prod,prod.size)}}</span>
                        <span class="cart_elem_prop" v-for="meta in JSON.parse(prod.meta)" :key="meta.name">{{$Helper.translateMetaKey(meta.name)}}: {{ meta.value }}</span>
                        <span class="cart_elem_prop">Количество: {{ prod.amount }} шт</span>

                    </div>
                    <div class="cart_elem_meta_group">
                        <div class="cart_elem_meta">
                            <span class="cart_elem_price">{{ ~~prod.price * ~~prod.amount }} р. <button @click="removeFromOrder(prod)">Удалить из заказа</button></span>
                        </div>
                    </div>
                </div>
            </div> 

            

            <h2>Способ доставки</h2>
            <input type="radio" name="delivery" id="r1" value="delivery_russia">
            <label class="radio-btn" for="r1">
                <img src="@/assets/icons/delivery_russia.svg" height="52" alt="Почта россии">
                <span>Почта России</span>
            </label>
            <input type="radio" name="delivery" id="r2" value="delivery_town">
            <label class="radio-btn" for="r2">
                <img src="@/assets/icons/delivery_town.svg" height="52" alt="Почта россии">
                <span>Доставка по городу</span>
            </label>
            <input type="radio" name="delivery" id="r3" value="delivery_company">
            <label class="radio-btn" for="r3">
                <img src="@/assets/icons/delivery_company.svg" height="52" alt="Почта россии">
                <span>Транспортная компания</span>
            </label>
            <input type="radio" name="delivery" id="r4" value="delivery_self">
            <label class="radio-btn" for="r4">
                <img src="@/assets/icons/delivery_self.svg" height="52" alt="Почта россии">
                <span>Самовывоз</span>
            </label>
            <h1>Общая сумма заказа: {{totalPrice}} рублей</h1>
            <button @click="goToPay()">Оплатить</button>
        </form>
        
    </div>
</template> 

<script>
import HeaderComp from '@/components/HeaderComp.vue';


    export default {
        name: 'OrderView',
        data(){
            return {
                totalPrice : 0,
                cart : []
            }
        },
        components:{
            HeaderComp
        },
        mounted(){ 
            let ref= this;
            this.cart = this.$CoreAPI.client_getCart();
            for(let prod of this.$CoreAPI.client_getCart()){ 
                console.log(prod);
                ref.totalPrice += ~~prod.price * ~~prod.amount;
            }

            if(this.$CoreAPI.client_getCart().length < 1){
                this.$router.push("/cart")
            }
            this.$CoreAPI.on("cart-updated",()=>{
                this.cart = this.$CoreAPI.client_getCart();
            })
        },
        methods: {
            goToPay(){
                let confirmedArray = [];
                let form = this.$refs.form;


                for(let prod of this.$CoreAPI.client_getCart()){
                    delete prod["price"];
                    delete prod["name"];
                    confirmedArray.push(prod);
                }

                let data ={
                    prods : JSON.stringify(confirmedArray),
                    fio : form.fio.value,
                    phone : form.phone.value,
                    email : form.email.value,
                    delivery : form.delivery.value
                }

                for(let i in data){
                    if(data[i].length < 1){
                        return alert("Проверьте заполненность всех полей");
                    }
                }


                this.$CoreAPI.client_clearCart();
                this.$CoreAPI.create_order(data).then((result)=>{
                    location.href = result.paylink;
                })
                
            },
            removeFromOrder(prod){
                this.$CoreAPI.client_removeFromCart(prod)
                if(this.$CoreAPI.client_getCart().length < 1){
                this.$router.push("/cart")
            }
            }
        },
    }
</script>
<style scoped lang="scss">
.order{
    font-size: 20px;
}
h1{
    font-weight: bold;
    font-size: 1.1em;
}

.cart_table{
    display: flex;
    flex-direction: column;
    width: 700px;
}
.cart_elem{
    border: 1px solid #dadada;
    display: flex;
    padding: 10px 10px;
    align-items: center;
    margin-top: 10px;
    column-gap: 2vw;
}
.cart_elem_meta{
    display: flex;
    flex-direction: row;
    column-gap: 1vw;
}
.cart_elem_name{
    font-weight: bold;
}
.cart_elem_price{
    margin-left: 1vw;
}

.cart_elem_prop{
    font-size: 0.8em;
}

.cart_elem_meta_group{
    display: flex;
    flex-direction: column;
}

.form{
    display: flex;
    flex-direction: column;
    width: 450px;
    row-gap: 1vh;
    margin-left: 2vw;
}
.form input{
    padding: 10px;
    background-color: #ebebeb;
}
input[type=radio]{
    display: none;
}
input[type=radio]:checked + .radio-btn{
    background-color: #e0f240;
}
.radio-btn{
    background-color: #ebebeb;
    padding: 5px;
    text-transform: uppercase;
    display: flex;
    align-items: center;
}
h2{
    text-transform: uppercase;
    font-weight: bold;
}
.radio-btn img{
    flex: 1;
}
.radio-btn span{
    flex: 5;
}
button{
    padding: 10px;
    &:hover{
        background: #dfdfdf;
        cursor: poiner;
    }
}
.cart_elem button{
    margin-left: 2vw;
    font-size: 0.8em;
}
</style>